/* istanbul ignore file */
import React, { Component } from 'react';
import styled from 'styled-components';

import TextField from '../TextField';

const StyledButton = styled.button`
  font-weight: 700;
  display: inline-block;
  text-align: center;
  position: relative;
  transition: all 0.2s ease;
  font-family: MarkProBold, MarkPro, sans-serif;
  text-transform: none;

  &:focus {
    outline: none;
  }
  background: rgb(253, 204, 8) !important;
  color: rgb(0, 0, 0) !important;
  border: 2px solid rgb(253, 204, 8);
  width: 100%;
  @media only screen and (min-width: 768px) {
    max-width: 320px;
  }
  font-size: 16px;
  padding: 11px 12px 13px;
`;
const StyledError = styled.p`
  color: rgb(217, 58, 27);
  font-size: 14px;
  line-height: 18px;
  font-weight: 700;
`

class OptusButton extends Component {
  constructor() {
    super();

    this.state = {};
  }

  componentDidMount() {}

  render() {
    return (
      <div>
        {this.props.prependInput ? <TextField onChange={this.props.prependInputChange} placeholder={this.props.prependInputPlaceholder}></TextField> : ''}
        {!this.props.inputError ? '' : (<StyledError>{this.props.inputError}</StyledError>)}
        <StyledButton {...this.props}>{this.props.text}</StyledButton>
      </div>
    );
  }
}

export default OptusButton;
